import React from 'react';
import './Livestream.css';
import * as firebase from 'firebase';
// ad nav links, view on yt
class Livestream extends React.Component {
	constructor(props) {
	    super(props);
	    this.nameRef = React.createRef();
	    this.commentRef = React.createRef();  
	    this.leaveComment = this.leaveComment.bind(this);

		this.state = {
			comments : [],
			some: ''
		};

      	var ref = firebase.database().ref().child('comments');
		ref.once('value')
		.then((snap) => {
			const commentObject = snap.val();
			const commentList = Object.keys(commentObject).map(key => ({
	          ...commentObject[key],
	          uid: key,
	        }));
	        this.setState({
	          comments: commentList,
	          some: "haha i don't know what i'm doing"
	        });
	        console.log(commentList)
			console.log(this.state.comments)
		});
	}

	leaveComment() {
		if (this.commentRef.current.value != "" && this.nameRef.current.value != "") {
			var commentData = {
				name: this.nameRef.current.value,
				comment: this.commentRef.current.value
			};

			this.commentRef.current.value = "";

			firebase.database().ref().child('comments').push(commentData);
			var ref = firebase.database().ref().child('comments');
			ref.once('value')
			.then((snap) => {
				const commentObject = snap.val();
				const commentList = Object.keys(commentObject).map(key => ({
		          ...commentObject[key],
		          uid: key,
		        }));
		        this.setState({
		          comments: commentList,
		          some: "haha i don't know what i'm doing"
		        });
		        console.log(commentList)
				console.log(this.state.comments)
			});
		}
	}

	render() {
		return (
			<div className="Livestream">
				<body className="body">
					<div className="video-container">
						<iframe width="100%" height="100%" src="https://www.youtube.com/embed/dQw4w9WgXcQ" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
					</div>
					<br /><br />
					<div className="comment-section">
						<h1>Comments ({this.state.comments.length})</h1>
						<div className="comment-form">
							<input name="commenterName" id="commenterName" ref={this.nameRef} className="input your-name" type="text" placeholder="Your name" required />
							<input name="comment" id="comment" ref={this.commentRef} className="input leave-a-comment" type="text" placeholder="Leave a comment" required />
							<button className="submit" onClick={this.leaveComment}>➔</button>
						</div>
						<ul>
							{this.state.comments.reverse().map(comment => {
						        return <li key={comment.key}><b>{comment.name}</b> {comment.comment}</li>
						    })}
						</ul>
					</div>
					<br /><br /> 
				</body>
			</div>
		)
	}
}

export default Livestream;